.Info {
    width: 97vw;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    margin-top: 5vh;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
}

.services {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 50vh;
}

.services > div {
    flex: 1 1 0px;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    margin: .5em;
}

.services .service-image {
    flex-grow: 10;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    transition: border 0.1s ease-in;
}

.service-title {
    margin: auto;
    color: rgba(0, 0, 0, 0.548);
    font-style: italic;
    font-size: 1.1rem;
}

.services div:not(.selected):hover .service-image {
    border-bottom: 3px solid #facb0b;
 }

.services .selected .service-image {
    border-bottom: 3px solid #facb0b;
 }

 .services .selected .service-title {
    color: #cf5524;
    font-weight: bold;
    font-style: italic;
    font-size: 1.1rem;
}

.service-description {
    height: auto;
    width: 100%;
    display: flex;
}

#personalized-consultation .service-image {
    background-image: url('../../../assets/personalized_consultation1.png');
}

#workshops .service-image {
    background-image: url('../../../assets/workshops1.png');
}

#colaborations .service-image {
    background-image: url('../../../assets/colaborations.png');
}


.card-col {
    width: max-content;
    margin: 1rem;
    display: inline-block;
}

.card-col-info {
    width: 25rem;
    font-size: .9rem;
    color: rgba(0, 0, 0, 0.548);
    margin-left: 1rem;
}

.form-col {
    display: inline-block;
    flex-grow: 2;
    margin: 1rem;
}

