.workshop-container {
    margin: 1.5rem;
    width: 100%;
}

.workshop-container .title {
    font-size: 1.3rem;
    color: rgba(0, 0, 0, 0.548);
    font-style: italic;
    margin-bottom: 1rem;
}

.workshop-container .title p {
    margin-bottom: 0px;
}

.workshop-container .columns {
    display: flex;
}

.workshop-container .colored-list {
    list-style-type: none;
    width: max-content;
    margin: auto;
}

.workshop-container .colored-list li {
    background-color: #f7f7f7;
    margin: 2.5rem .75rem;
    padding: .2rem .5rem;
    text-align: center;
    font-size: 1.3rem;
    cursor: default;
}

.workshop-container .colored-list li:nth-child(even) {
    color: #6cc6c6;
}

.workshop-container .colored-list li:nth-child(odd) {
    color: #d8e994;
}

.workshop-container .form {
    padding-left: 3rem;
    border-left: 1px #facb0b solid;
}

.workshop-container .detail {
    color: rgba(0, 0, 0, 0.548);
}

.workshop-container .detail div {
    margin-top: 2rem;
    font-size: 1.2rem;
}

.workshop-container .detail li {
    margin: .5rem;
    color: rgba(0, 0, 0, 0.548);
}