.service-form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.form-element {
    display: block;
}

.form-actions {
    display: flex;
    justify-content: flex-end;
    margin: 2rem;
}

.form-title {
    font-weight: bold;
    margin-bottom: .7rem;
    color: #cf5524;
}