.Contact {
    width: 90%;
    margin: 0 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    font-size: calc(10px + 2vmin);
    color: black;
}

.Contact .profile {
    display: flex;
    justify-content: space-evenly;
    height: min-content;
    padding: 2em 0em;
}

.Contact h5,
.Contact h6 {
    color: #cf5524;
    font-weight: bold;
    font-style: italic;
  }

.svg-inline--fa {
    padding: 5px;
}

.profile-img {
    display: none;
    width: 40%;
    border-radius: 5%;
    margin: auto;
}

.profile-info {
    height: min-content;
    margin: auto;
}

.social {
    display: flex;
}