.Footer {
    width: 100%;
    display: flex;
    background-color: #cf5524;
    padding: .5em;
    justify-content: space-between;
    align-items: baseline;
}

.Footer .icons {
    display: flex;
}

.Footer .icons .round-icon {
    padding: .2em;
    margin: .2em;
    background-color: #fcb404;
    border-radius: 100%;
    color: white;
}

.Footer h6 {
    color: #fcb404;
    font-weight: bold;
    font-style: italic;
}