body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Main {
  background: #fff url(/web-test/static/media/main_background_light.0a052866.jpg) no-repeat center;
  background-size: cover;
  height: 90vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.Main {
    background: #fff url(/web-test/static/media/main_background_light.0a052866.jpg) no-repeat center;
    background-size: cover;
    height: 90vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding-bottom: 5em;
}

.main-label {
    margin: 1em;
    color: white;
    font-size: 1.5em;
    font-weight: bold;
}
.About {
    margin: 1rem 1rem;
    display: flex;
    flex-direction: column;
}

.frame {
    margin: 2rem 0rem;
}

.About .profile {
    height: 50%;
    border-radius: 5%;
    width: 50%;
    margin: auto;
}

blockquote{
    font-size: 1em;
    width:100%;
    margin:50px auto;
    font-family:Open Sans;
    font-style:italic;
    color: #555555;
    padding:1.2em 30px 1.2em 75px;
    border-left:8px solid #facb0b ;
    line-height:1.6;
    position: relative;
    background:#EDEDED;
  }
  
  blockquote::before{
    font-family:Arial;
    content: "\201C";
    color:#facb0b;
    font-size:4em;
    position: absolute;
    left: 10px;
    top:-10px;
  }
  
  blockquote::after{
    content: '';
  }
  
  blockquote span{
    display:block;
    color:#333333;
    font-style: normal;
    font-weight: bold;
    margin-top:1em;
  }

  .About h3,
  .About h4 {
    color: #cf5524;
    font-weight: bold;
    font-style: italic;
  }

  .About p,
  .About li {
    color: rgba(0, 0, 0, 0.548);
    font-size: 1rem;
  }
.service-form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.form-element {
    display: block;
}

.form-actions {
    display: flex;
    justify-content: flex-end;
    margin: 2rem;
}

.form-title {
    font-weight: bold;
    margin-bottom: .7rem;
    color: #cf5524;
}
.Contact {
    width: 90%;
    margin: 0 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    font-size: calc(10px + 2vmin);
    color: black;
}

.Contact .profile {
    display: flex;
    justify-content: space-evenly;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    padding: 2em 0em;
}

.Contact h5,
.Contact h6 {
    color: #cf5524;
    font-weight: bold;
    font-style: italic;
  }

.svg-inline--fa {
    padding: 5px;
}

.profile-img {
    display: none;
    width: 40%;
    border-radius: 5%;
    margin: auto;
}

.profile-info {
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    margin: auto;
}

.social {
    display: flex;
}
.consultation-container {
    margin: 1.5rem;
    width: 100%;
}

.consultation-container .title {
    font-size: 1.3rem;
    color: rgba(0, 0, 0, 0.548);
    font-style: italic;
    margin-bottom: 1rem;
}

.consultation-container .title p {
    margin-bottom: 0px;
}

.consultation-container .columns {
    display: flex;
}

.consultation-container .column.main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 80%;
    align-items: center;
}

.consultation-container .column.detail {
    margin: 0rem 3rem;
}

.consultation-container .actions {
    margin: 2rem 0;
}

.consultation-container .actions Button {
    margin: 0rem .5rem;
}

.consultation-container .colored-list {
    list-style-type: none;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

.consultation-container .colored-list li {
    background-color: #f7f7f7;
    padding: .2rem .5rem;
    margin: 1.2rem;
    text-align: center;
    font-size: 1.3rem;
    cursor: pointer;
}

.consultation-container .colored-list li.selected,
.consultation-container .colored-list li:hover {
    font-weight: bold;
}

.consultation-container .colored-list li:nth-child(even) {
    color: #6cc6c6;
}

.consultation-container .colored-list li:nth-child(odd) {
    color: #d8e994;
}

.consultation-container .form {
    padding-left: 3rem;
    border-left: 1px #facb0b solid;
}

.consultation-container .detail ul li {
    margin: 1rem .5rem;
    color: rgba(0, 0, 0, 0.548);
    font-size: 1.2rem;
}
.workshop-container {
    margin: 1.5rem;
    width: 100%;
}

.workshop-container .title {
    font-size: 1.3rem;
