}

.workshop-container .colored-list {
    list-style-type: none;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    margin: auto;
}

.workshop-container .colored-list li {
    background-color: #f7f7f7;
    margin: 2.5rem .75rem;
    padding: .2rem .5rem;
    text-align: center;
    font-size: 1.3rem;
    cursor: default;
}

.workshop-container .colored-list li:nth-child(even) {
    color: #6cc6c6;
}

.workshop-container .colored-list li:nth-child(odd) {
    color: #d8e994;
}

.workshop-container .form {
    padding-left: 3rem;
    border-left: 1px #facb0b solid;
}

.workshop-container .detail {
    color: rgba(0, 0, 0, 0.548);
}

.workshop-container .detail div {
    margin-top: 2rem;
    font-size: 1.2rem;
}

.workshop-container .detail li {
    margin: .5rem;
    color: rgba(0, 0, 0, 0.548);
}
.colaborations-container {
    margin: 1.5rem;
    width: 100%;
}

.colaborations-container .title {
    font-size: 1.3rem;
    color: rgba(0, 0, 0, 0.548);
    font-style: italic;
    margin-bottom: 1rem;
}

.colaborations-container .title p {
    margin-bottom: 0px;
}

.colaborations-container .columns {
    display: flex;
}

.colaborations-container .colored-list {
    list-style-type: none;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

.colaborations-container .colored-list li {
    background-color: #f7f7f7;
    margin: 2.5rem .75rem;
    padding: .2rem .5rem;
    text-align: center;
    font-size: 1.3rem;
    cursor: default;
}

.colaborations-container .colored-list li:nth-child(even) {
    color: #6cc6c6;
}

.colaborations-container .colored-list li:nth-child(odd) {
    color: #d8e994;
}

.colaborations-container .form {
    padding-left: 3rem;
    border-left: 1px #facb0b solid;
}

.colaborations-container .detail {
    color: rgba(0, 0, 0, 0.548);
}

.colaborations-container .detail p {
    font-size: 1rem;
}

.colaborations-container .detail .actions {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.colaborations-container img {
    width: 30%;
    margin: 2rem 0rem;
}
.Info {
    width: 97vw;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    margin-top: 5vh;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
}

.services {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 50vh;
}

.services > div {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    margin: .5em;
}

.services .service-image {
    flex-grow: 10;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    transition: border 0.1s ease-in;
}

.service-title {
    margin: auto;
    color: rgba(0, 0, 0, 0.548);
    font-style: italic;
    font-size: 1.1rem;
}

.services div:not(.selected):hover .service-image {
    border-bottom: 3px solid #facb0b;
 }

.services .selected .service-image {
    border-bottom: 3px solid #facb0b;
 }

 .services .selected .service-title {
    color: #cf5524;
    font-weight: bold;
    font-style: italic;
    font-size: 1.1rem;
}

.service-description {
    height: auto;
    width: 100%;
    display: flex;
}

#personalized-consultation .service-image {
    background-image: url(/web-test/static/media/personalized_consultation1.bcb187ce.png);
}

#workshops .service-image {
    background-image: url(/web-test/static/media/workshops1.f46159c5.png);
}

#colaborations .service-image {
    background-image: url(/web-test/static/media/colaborations.496bcc85.png);
}


.card-col {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    margin: 1rem;
    display: inline-block;
}

.card-col-info {
    width: 25rem;
    font-size: .9rem;
    color: rgba(0, 0, 0, 0.548);
    margin-left: 1rem;
}

.form-col {
    display: inline-block;
    flex-grow: 2;
    margin: 1rem;
}


.Consultations {
    height: 0vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-content: center;
    overflow: hidden;
    transition: height 0.5s linear;
}

.Consultations .columns {
    display: flex;
}

.Consultations .fees {
    margin: auto;
}

.Consultations p,
.Consultations ul {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    margin: 1rem auto;
    list-style: none;
}

.Consultations ul li:before {
    content: '\2713';
    font-size: 1.5rem;
  }

.Consultations p {
    background-color: #f7f7f7;
    padding: .2rem .5rem;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    font-size: 1.3rem;
}

.first-session p {
    color: #d8e994;
}

.next-sessions p {
    color: #6cc6c6;
}

.Consultations .column ul li {
    color: rgba(0, 0, 0, 0.548);
    font-size: 1.2rem;
}
.Footer {
    width: 100%;
    display: flex;
    background-color: #cf5524;
    padding: .5em;
    justify-content: space-between;
    align-items: baseline;
}

.Footer .icons {
    display: flex;
}

.Footer .icons .round-icon {
    padding: .2em;
    margin: .2em;
    background-color: #fcb404;
    border-radius: 100%;
    color: white;
}

.Footer h6 {
    color: #fcb404;
    font-weight: bold;
    font-style: italic;
}
.Panel {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
nav {
    height: 45%;
    width: 100%;
    margin: 0;
    top: 0;
    color: rgba(0, 0, 0, 0.548);
    background-color: white;
}

.logo-big {
   width: 100%;
   height: 85%;
   background: #f7f7f7 url(/web-test/static/media/logo.df600be6.png) no-repeat center;
   text-align: center;
}

.fixed-nav ul {
    height: 15%;
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
}

.fixed-nav ul.colored {
    background-color: #f7f7f7
}

.fixed-nav li {
    flex: 1 1;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in;
}

.fixed-nav li.hidden {
    display: none;
}

.fixed-nav li:not(.main-button) {
    cursor: pointer;
}

.fixed-nav li.main-button {
    padding-left: 10px;
    flex-grow: 1;
    justify-content: flex-start;
}

.fixed-nav li:not(.main-button):hover {
    border-bottom: 3px solid #facb0b;
    transition: all 0.1s ease-in;
}

.fixed-nav {
    display: block;
    position: fixed;
    z-index: 1;
}

.fixed-nav .navbar-logo {
    height: 100%;
    cursor: pointer;
}
