.Consultations {
    height: 0vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-content: center;
    overflow: hidden;
    transition: height 0.5s linear;
}

.Consultations .columns {
    display: flex;
}

.Consultations .fees {
    margin: auto;
}

.Consultations p,
.Consultations ul {
    width: max-content;
    margin: 1rem auto;
    list-style: none;
}

.Consultations ul li:before {
    content: '✓';
    font-size: 1.5rem;
  }

.Consultations p {
    background-color: #f7f7f7;
    padding: .2rem .5rem;
    width: max-content;
    font-size: 1.3rem;
}

.first-session p {
    color: #d8e994;
}

.next-sessions p {
    color: #6cc6c6;
}

.Consultations .column ul li {
    color: rgba(0, 0, 0, 0.548);
    font-size: 1.2rem;
}