.About {
    margin: 1rem 1rem;
    display: flex;
    flex-direction: column;
}

.frame {
    margin: 2rem 0rem;
}

.About .profile {
    height: 50%;
    border-radius: 5%;
    width: 50%;
    margin: auto;
}

blockquote{
    font-size: 1em;
    width:100%;
    margin:50px auto;
    font-family:Open Sans;
    font-style:italic;
    color: #555555;
    padding:1.2em 30px 1.2em 75px;
    border-left:8px solid #facb0b ;
    line-height:1.6;
    position: relative;
    background:#EDEDED;
  }
  
  blockquote::before{
    font-family:Arial;
    content: "\201C";
    color:#facb0b;
    font-size:4em;
    position: absolute;
    left: 10px;
    top:-10px;
  }
  
  blockquote::after{
    content: '';
  }
  
  blockquote span{
    display:block;
    color:#333333;
    font-style: normal;
    font-weight: bold;
    margin-top:1em;
  }

  .About h3,
  .About h4 {
    color: #cf5524;
    font-weight: bold;
    font-style: italic;
  }

  .About p,
  .About li {
    color: rgba(0, 0, 0, 0.548);
    font-size: 1rem;
  }